/**
 * @module fontsizes
 * @desc Font sizes tokens module.
 *
 * @memberof @gympass/yoga-tokens
 */

/**
 * A font size
 * @typedef FontSize
 *
 * @type {Object}
 * @property {number} xxsmall - 10
 * @property {number} xsmall - 12
 * @property {number} small - 14
 * @property {number} medium - 16
 * @property {number} large - 20
 * @property {number} xlarge - 24
 * @property {number} xxlarge - 32
 */

/**
 * @type {FontSize}
 * @default
 */
var fontSizes = [10, 12, 14, 16, 20, 24, 32];
fontSizes.xxsmall = fontSizes[0];
fontSizes.xsmall = fontSizes[1];
fontSizes.small = fontSizes[2];
fontSizes.medium = fontSizes[3];
fontSizes.large = fontSizes[4];
fontSizes.xlarge = fontSizes[5];
fontSizes.xxlarge = fontSizes[6];
export default fontSizes;