/**
 * @module spacing
 * @desc Spacing tokens module.
 *
 * @memberof @gympass/yoga-tokens
 */

/**
 * A spacing
 * @typedef Spacing
 *
 * @type {Object}
 * @property {number} zero - 0
 * @property {number} xxsmall - 4
 * @property {number} xsmall - 8
 * @property {number} small - 12
 * @property {number} medium - 16
 * @property {number} large - 20
 * @property {number} xlarge - 24
 * @property {number} xxlarge - 40
 * @property {number} xxxlarge - 56
 * @property {number} huge - 72
 */

/**
 * @type {Spacing}
 * @default
 */
var spacing = [0, 4, 8, 12, 16, 20, 24, 40, 56, 72];
spacing.zero = spacing[0];
spacing.xxsmall = spacing[1];
spacing.xsmall = spacing[2];
spacing.small = spacing[3];
spacing.medium = spacing[4];
spacing.large = spacing[5];
spacing.xlarge = spacing[6];
spacing.xxlarge = spacing[7];
spacing.xxxlarge = spacing[8];
spacing.huge = spacing[9];
export default spacing;